











export default {
	data() {
		return{
			title:"text",
			locationtype:'gcj02',
			latitude: 24.836836 ,
			longitude: 102.847126,
			covers: [{
				latitude: 24.836836,
				longitude: 102.847126,
				iconPath: '../../static/location@3x.png'
			}]
		}	
	},
	methods: {
		clean:function(){
			uni.clearStorageSync();
			uni.showToast({
				title: '清除成功！',
				duration: 2000
			});
		},
		getLocation:function(){
			uni.getLocation({
				type: this.locationtype,
				success: function (res) {
				uni.showToast({
					title: '获取位置成功！',
					duration: 2000
				});
				console.log('当前位置的经度：' + res.longitude);
				console.log('当前位置的纬度：' + res.latitude);
				}
			});
		}
	}
}

